@font-face {
  font-family: "Montserrat";
  src: url("/fonts/montserrat-regular.woff2") format("woff2"), url("/fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("/fonts/montserrat-medium.woff2") format("woff2"), url("/fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("/fonts/montserrat-semibold.woff2") format("woff2"), url("/fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("/fonts/montserrat-bold.woff2") format("woff2"), url("/fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Calibri", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #fdfbf9;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #fdfbf9;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.menu-opened {
  overflow: hidden;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023.2px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767.2px) {
  .container {
    padding: 0 15px;
  }
}

.header .container,
.footer .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header .container {
  padding-top: 35px;
  padding-bottom: 35px;
}
@media (max-width: 1023.2px) {
  .header .container {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
@media (max-width: 767.2px) {
  .header .container {
    padding-top: 20px;
    padding-bottom: 20px;
    flex-direction: column;
  }
}

.header--menu-opened .container {
  margin-right: 0;
  height: 100vh;
  width: calc(100% - 36px);
  justify-content: flex-start;
  background-color: #fdfbf9;
}

@media (max-width: 767.2px) {
  .header--no-js .container {
    margin-right: 0;
    justify-content: flex-start;
    background-color: #fdfbf9;
  }
}

.footer .container {
  margin-right: auto;
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (max-width: 767.2px) {
  .footer .container {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.burger {
  display: none;
}
.header--no-js .burger {
  display: none;
}
@media (max-width: 767.2px) {
  .burger {
    position: absolute;
    display: inline-block;
    padding: 10px;
    right: 0;
    box-sizing: border-box;
    border: none;
    background-color: transparent;
  }
}

.burger__cross {
  position: relative;
  display: block;
  width: 24px;
  height: 2px;
  background-color: #fdfbf9;
}
.burger__cross::after, .burger__cross::before {
  position: absolute;
  width: 24px;
  height: 2px;
  left: 0;
  background-color: #fdfbf9;
  content: "";
  transform: rotate(0deg);
  transition: transform 0.3s ease;
}
.burger__cross::after {
  top: 5px;
}
.burger__cross::before {
  bottom: 5px;
}

.header--menu-opened .burger__cross::after, .header--menu-opened .burger__cross::before {
  width: 19px;
  transform-origin: center right;
  background-color: #011c40;
}
.header--menu-opened .burger__cross::after {
  top: 7px;
  transform: rotate(45deg);
}
.header--menu-opened .burger__cross::before {
  bottom: 7px;
  transform: rotate(-45deg);
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  width: 530px;
  padding: 14px 0 28px;
}
@media screen and (max-width: 767px) {
  .custom-input,
  .custom-textarea {
    padding: 4px 0 48px;
    width: 100%;
  }
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
  .custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  max-width: 300px;
  min-height: 48px;
  margin-left: auto;
  padding: 10px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
  .custom-input label textarea,
  .custom-textarea label input,
  .custom-textarea label textarea {
    max-width: unset;
    width: 100%;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label,
  .custom-textarea__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error,
  .custom-textarea__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;
  display: flex;
  width: 500px;
  padding: 14px 0 10px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle {
    padding: 14px 0 10px 0;
    width: 100%;
  }
}
.custom-toggle:last-of-type {
  padding: 14px 0 58px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle:last-of-type {
    padding: 14px 0 58px 0;
  }
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 52px;
  cursor: pointer;
}
.custom-toggle label span {
  margin-bottom: 0;
  margin-left: 0;
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1.5px solid #2c39f2;
  transition: border 0.3s ease;
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background-color: rgba(44, 57, 242, 0);
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1px solid #2c39f2;
  border-radius: 50%;
  transition: border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  color: rgba(255, 255, 255, 0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: color 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon {
  border: 1.5px solid #2c39f2;
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  background-color: #2c39f2;
}
.custom-toggle.is-invalid label span {
  color: #ff1553;
}
.custom-toggle .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-toggle .input-message.is-invalid {
  color: #ff0000;
}
.custom-toggle .input-message.is-valid {
  color: #15ab07;
}

.footer {
  display: flex;
  background-color: #011c40;
}

.header {
  position: absolute;
  display: flex;
  width: 100%;
  z-index: 1;
}
.header--menu-opened {
  width: 100%;
  height: 100vh;
  background: rgba(1, 28, 64, 0.5);
}
.header--no-js {
  position: static;
}

.intro {
  display: flex;
  padding: 198px 95px;
  min-height: 650px;
  flex-direction: column;
  justify-content: center;
  background-color: #011c40;
  color: #fdfbf9;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-image: image-set(url("../img/backgrounds/hero.webp") 1x, url("../img/backgrounds/hero@2x.webp") 2x, url("../img/backgrounds/hero.jpg") 1x, url("../img/backgrounds/hero@2x.jpg") 2x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.intro h1 {
  margin: 0;
  padding-bottom: 15px;
  font-size: 54px;
  line-height: 66px;
}
.intro p {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
}
@media (min-width: 1024px) {
  .intro {
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  }
}
@media (max-width: 1023.2px) {
  .intro {
    min-height: 638px;
    padding: 45px 0;
    background-image: image-set(url("../img/backgrounds/hero_tablet.webp") 1x, url("../img/backgrounds/hero_tablet@2x.webp") 2x, url("../img/backgrounds/hero_tablet.jpg") 1x, url("../img/backgrounds/hero_tablet@2x.jpg") 2x);
  }
  .intro h1 {
    font-size: 50px;
    line-height: 61px;
  }
  .intro p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767.2px) {
  .intro {
    min-height: 500px;
    background-image: image-set(url("../img/backgrounds/hero_mobile.webp") 1x, url("../img/backgrounds/hero_mobile@2x.webp") 2x, url("../img/backgrounds/hero_mobile.jpg") 1x, url("../img/backgrounds/hero_mobile@2x.jpg") 2x);
  }
  .intro h1 {
    padding-bottom: 10px;
    font-size: 36px;
    line-height: 45px;
  }
  .intro p {
    font-size: 16px;
    line-height: 22px;
  }
}

.logo {
  display: inline-block;
  margin-right: 40px;
  color: #fdfbf9;
}
@media (max-width: 1023.2px) {
  .logo {
    margin-right: 25px;
  }
}

@media (max-width: 767.2px) {
  .header--no-js .logo,
  .header--menu-opened .logo {
    color: #011c40;
  }
}

.logo__icon {
  width: 70px;
  height: 38px;
