  }
}

@media (max-width: 767.2px) {
  .header--menu-opened .navigation-list {
    display: flex;
    padding: 28px 0;
    filter: none;
  }
}

.footer .navigation-list {
  margin-right: auto;
}
@media (max-width: 1023.2px) {
  .footer .navigation-list {
    font-size: 12px;
    line-height: 15px;
  }
}

.navigation-list__item {
  display: flex;
}

@media (max-width: 767.2px) {
  .navigation-list__link {
    padding: 10px;
  }
}

.social-list {
  display: flex;
  margin: 0 0 0 30px;
  padding: 0;
  flex-wrap: wrap;
  list-style: none;
  gap: 7px;
}
@media (max-width: 1023.2px) {
  .social-list {
    gap: 2px;
  }
}

.social-list__link {
  display: inline-block;
  padding: 10px;
}

.social-list__icon {
  height: 20px;
}
@media (max-width: 1023.2px) {
  .social-list__icon {
    height: 20px;
  }
}

